@import "./mixin";

.default-theme {
	$sidebar-bg-color: #1d1d1d;
	$primary-highlight-color: #e7e7e7;
	$secondary-highlight-color: #78385f;
	$border-color: #2b2b2b;
	$submenu-bg-color: #2b2b2b;
	$menu-icon-bg-color: #2b2b2b;
	$menu-icon-color: #818896;
	$primary-icon-highlight-color: #a9dff4;
	$menu-header-color: #868686;
	$search-input-bg-color: #2b2b2b;
	$footer-bg-color: #2b2b2b;
	$shadow-color: #1d1d1d;
	$scrollbar-color: #525965;
	$primary-color: #151640;
	$primary-color1: #161f2f;
	$primary-light: #5e6f7f;
	$secondary-color: #df5c11;
	@include themes(
		$sidebar-bg-color,
		$primary-highlight-color,
		$secondary-highlight-color,
		$border-color,
		$submenu-bg-color,
		$menu-icon-bg-color,
		$menu-icon-color,
		$primary-icon-highlight-color,
		$menu-header-color,
		$search-input-bg-color,
		$footer-bg-color,
		$shadow-color,
		$scrollbar-color,
		$primary-color,
		$primary-color1,
		$primary-light,
		$secondary-color
	);
}
$sidebar-bg-color: #2a2c2b;
$primary-color: #d9cb9e;
$primary-highlight-color: #ffe79a;
$border-color: #374140;
$submenu-bg-color: #374140;
$menu-icon-bg-color: #374140;
$menu-icon-color: #d9cb9e;
$menu-icon-highlight-color: #ffbe00;
$menu-header-color: #a29464;
$search-input-bg-color: #374140;
$footer-bg-color: #374140;
$shadow-color: #2a2c2b;
$scrollbar-color: #525965;

